.miniChart {
  position: relative;
  width: 100%;
}
.miniChart .chartContent {
  position: relative;
  bottom: -4px;
  width: 100%;
  height: 5.7vh;
}
.miniChart .chartContent > div {
  margin: 0 -5px;
  overflow: hidden;
}
.miniChart .chartLoading {
  position: absolute;
  top: 16px;
  left: 50%;
  margin-left: -7px;
}
.miniChart .custom-tooltip {
  width: '100%';
  background-color: black;
  text-align: center;
  border-radius: 2px;
}
.miniChart .custom-tooltip .label {
  color: white;
}
